import React, { useState, useEffect } from "react";
import {Container, Row, Col, Button, ProgressBar, Spinner} from "react-bootstrap";
import { toast } from 'react-toastify'; //toster
import 'react-toastify/dist/ReactToastify.css';
import { useParams } from "react-router-dom";
import './Style.css';
//connect wallet button
import ConnectWallet from "../components/Wallet/connectWallet";
//contribute function call
import { contributeETH } from "../components/contracts/ETHContributeCAll";
import {contributeBSC} from "../components/contracts/BSCContributeCAll";
//utils function 
import { isActive,getDealGoesLiveIn, getDealEndIn } from "./utils";
//sample data
// import { getDeal } from "./data";

const Contribute = ()=>{  
    const { id } = useParams();
    const [disableContribute,setDisableContribute] = useState(false);//Loading bar
    const [dealsDetails,setDealsDetails] = useState([{}]);//Deatils of deal
    const [dealStartTime,seDealStartTime] = useState('');//dealStartTime
    const [dealEndTime,setDealEndTime] = useState('');//dealEndTime
    const [loading,setLoading] = useState(false);//Loading bar
    //contribute button min, max, values
    const [contributeValue, setContributeValue] = useState({
        value:0,
        max:0
    })

    //import hooks for object
    const [walletData, setWalletData] = useState({
        walletAddress:"",
        balance:0,
        isConnected:false,
        networkName:"",
        ETHTokenCount:0,
        BSCTokenCount:0,
        TotalTokenCount:0,
        tier:0
    })

    const getDetails= async ()=>{
        // API call to fetch deals
        //Test data
        // setDealsDetails(getDeal)
        await fetch('https://ido-api-services.herokuapp.com/get-single-deal/'+id+'?filter={"offset": 0,"limit": 100,"skip": 0,"order": "string","fields": {"Name": true,"id": true,"Ticker": true,"TotalRaize": true,"Currency": true,"Start": true,"End": true,"Price": true,"Vesting": true,"DealRate": true,"AllocationForTier1": true,"AllocationForTier2": true,"AllocationForTier3": true,"AllocationForTier4": true,"Description": true,"Icon": true,"CurrentRaize": true,"WalletAddress": true}}').then(res=>res.json()).then(
          (result)=>{
                if(result.success){
                    //set return data into dealsDetails object
                    setDealsDetails(result.res);
                    console.log(result.res)
                } else {
                    console.log('Error while loading deal!');
                    toast.error("Error while loading deal!");
                }
            }
        )
    }

    useEffect(()=>{
        if(dealsDetails.CurrentRaize<dealsDetails.TotalRaize){
            //set times
            setInterval(()=>{
                let result=getDealGoesLiveIn(dealsDetails.Start)
                if(result)
                    seDealStartTime(result)
                },1000);
            setInterval(()=>{
                let result=getDealEndIn(dealsDetails.End)
                if(result)
                    setDealEndTime(result)
            },1000);
            if(isActive(dealsDetails.Start,dealsDetails.End))
                setDisableContribute(false)
            else
                setDisableContribute(true)
        } else{
            seDealStartTime('Closed')
            setDealEndTime('Closed')    
            setDisableContribute(true)
        }
    },[dealsDetails]);

    useEffect(()=>{
        setLoading(true)
        getDetails();
        setLoading(false)
    },[]);

    useEffect(()=>{
        //set input contribute field
        document.getElementById('contributionAmount').value=getYourMaxAllocaion(walletData.tier);
        document.getElementById('contributionAmount').max=getYourMaxAllocaion(walletData.tier);
    },[walletData.tier]);

    //Contribute Button call
    const contribute= async ()=>{
        setLoading(true)
        // Smart contract call to fetch deals
        if(walletData.isConnected){
            //if tier 0 then ask to stack token first
            if(walletData.tier==0){
                toast.error("You don't have tokens in stake")
            } else{
                const value = document.getElementById('contributionAmount').value;
                if(value<=getYourMaxAllocaion(walletData.tier) && value>0){
                    // alert("contribute button click")
                    var sucessCall=false;
                    //Contract call
                    if(walletData.networkName=="Eth"){
                        //call ethereum smart contract
                        sucessCall = await contributeETH(walletData.walletAddress,value,dealsDetails.WalletAddress)
                    } else if(walletData.networkName=="BSC"){
                        //call BSC smart contract
                        sucessCall = await contributeBSC(walletData.walletAddress,value,dealsDetails.WalletAddress)
                    }
                    if(sucessCall){ //if smart contract updated then only
                        //API Call to update contributed amount
                        let jsonData=dealsDetails;
                        delete jsonData.id //for API call data should be without id
                        jsonData.CurrentRaize=parseFloat(jsonData.CurrentRaize)+parseFloat(value);
                        // setDealsDetails(jsonData) //for Test data 
                        console.log(jsonData)
                        toast.success('Contributed Successfully!')
                        fetch('http://[::1]:3000/update-deals/'+id, {
                            method: 'post',
                            headers: { 'Content-Type': 'application/json' },
                            body: JSON.stringify(
                                    jsonData
                                ),
                            }).then((response) => response.json())
                            .then((responseJson) => {
                            console.log(responseJson);
                            })
                            .catch((error) => {
                            console.error(error);
                            });
                    }
                } else{
                    console.log("Value should be less than max Allocation and more than 0 !")
                    toast.error("Value should be less than max Allocation and more than 0 !")
                }    
            }
        } else{
            console.log('Please connect your wallet first!')
            toast.error("Please connect your wallet first!")
        }
        //uppdate deal details
        //set Loading false
        setLoading(false)
    }

    const getYourMaxAllocaion= (tier)=>{
        let result=0;
        //Set according to its tier
        switch(tier){
            case 1: result=dealsDetails.AllocationForTier1;
                    break;
            case 2: result=dealsDetails.AllocationForTier2;
                    break;
            case 3: result=dealsDetails.AllocationForTier3;
                    break;
            case 4: result=dealsDetails.AllocationForTier4;
                    break;
            default: result=0;
        }
        //Check for remaning tokens
        let remaning=dealsDetails.TotalRaize - dealsDetails.CurrentRaize;
        if(remaning<result)
            result=remaning;
        return result;
    }
    
    return(<>

<div className={loading ? 'parentDisable' : ''} width="100%">
    <div className={loading ? 'overlay-box' : 'hide'}>
        <Spinner animation="border" role="status">
            <span className="visually-hidden">Loading...</span>
        </Spinner>
    </div>


<div className="dashboard">
<Container className="maine-content">

<h2>Deal Details</h2>

{/* Deals Cards */}
<div className="tab-content">
<Row>

<Col md={4}> 
<div className="user-name-row dl-dt">
<img src={"data:image/png;base64,"+dealsDetails.Icon}/>
<p>{dealsDetails.Name} <span>{dealsDetails.Ticker}</span> </p>
</div>
</Col>

<Col md={4}>
<div class="row-box"><p class="text-heading">Deal goes live in</p><p class="amount">{dealStartTime}</p></div>
<div class="row-box dl-dt"><p class="text-heading">Deal Ends in</p><p class="amount">{dealEndTime}</p></div>
</Col>

<Col md={4}>
<div className="connect-wallet">
    {/* Connect Wallet Button */}
    <ConnectWallet walletData={walletData} setWalletData={setWalletData}></ConnectWallet>
</div>

</Col>
</Row>

<Row className="deal-row">
<Col md={4}>

</Col>

<Col md={8}>

</Col>
</Row>

<Row className="deal-row">
<Col md={12}>
<div className="tab-col">
<div className="heading-sm">Deal Progress</div> 
<Row className="total-box">
<Col md={4}><div class="row-box mt-0"><p class="text-heading">No. of Tokens</p><p class="amount">{dealsDetails.CurrentRaize}</p></div></Col>  
<Col md={4}><div class="row-box mt-0"><p class="text-heading">Total Amount contributed</p><p class="amount">{dealsDetails.TotalRaize}</p></div></Col> 
<Col md={4}>
<div class="row-box mt-0"><p class="text-heading">Rate</p><p class="amount">{dealsDetails.DealRate}</p></div>

</Col>  
</Row>  
<div className="progress-bar-row">
<ProgressBar now={(dealsDetails.CurrentRaize/dealsDetails.TotalRaize)*100} />
</div> 
<div className="finished-amt">
<p>{dealsDetails.CurrentRaize/dealsDetails.TotalRaize * 100}%</p>  <p className="amt">{dealsDetails.CurrentRaize}/{dealsDetails.TotalRaize}</p> 
</div>
</div>
</Col>
</Row>


<Row className="deal-row">
<Col md={3}>
    <input type="number" id="contributionAmount" min="0" step="0.5"></input>
        <Button className="cnt-button button-strict" onClick={contribute} disabled={disableContribute}> Contribute </Button>
</Col>

<Col md={9}>
<div className="tab-col">
 
<Row className="total-box">
<Col md={2}>
<div className="row-box">
<p className="text-heading">Your Tier: </p> 
<p className="amount">Tier {walletData.tier}</p>  
</div> 
</Col> 
<Col md={6}>
<div className="row-box">
<p className="text-heading">Token locked(BSC):</p> 
<p className="amount">{walletData.BSCTokenCount} BULK</p>  
</div>

<div className="row-box">
<p className="text-heading">Token locked(ETH):</p> 
<p className="amount">{walletData.ETHTokenCount} BULK</p>  
</div>

<div className="row-box">
<p className="text-heading">Total:</p> 
<p className="amount">{walletData.TotalTokenCount.toLocaleString()} BULK</p>  
</div> 
</Col> 

<Col md={4}>
<div className="row-box">
<p className="text-heading">Your max Allocaion:</p> 
    <p className="amount">{getYourMaxAllocaion(walletData.tier)}</p>  
</div>  
<div class="row-box mt-0"><p class="text-heading">(Amount in {dealsDetails.Currency})</p></div>  
</Col>

</Row>  
</div>
</Col>
</Row>



<div className="tab-col">
<Row>
<Col md={4}>
<div class="deal-info">
<div className="heading-sm">Deal Info</div> 
<ul>
<li><span className='text-heading'>Start:</span> {new Date(dealsDetails.Start).toUTCString()} </li>  
<li><span className='text-heading'>End:</span> {new Date(dealsDetails.End).toUTCString()} </li>  
<li><span className='text-heading'>Currency:</span> {dealsDetails.Currency} </li>  
<li><span className='text-heading'>Price:</span> {dealsDetails.Price}</li> 
<li><span className='text-heading'>Vesting:</span> {dealsDetails.Vesting}</li>  
</ul>
</div> 
</Col> 

<Col md={5}>
<div class="deal-info">
<div className="heading-sm">Allocation per contributor</div>
<ul>
<li><span className='text-heading'>Tier 1</span>: {dealsDetails.AllocationForTier1} {dealsDetails.Currency}</li>
<li><span className='text-heading'>Tier 2</span>: {dealsDetails.AllocationForTier2} {dealsDetails.Currency}</li>  
<li><span className='text-heading'>Tier 3</span>: {dealsDetails.AllocationForTier3} {dealsDetails.Currency}</li>  
<li><span className='text-heading'>Tier 4</span>: {dealsDetails.AllocationForTier4} {dealsDetails.Currency}</li>    
</ul>
</div> 
</Col>  

<Col md={3}>
<div className="col-100">
<Button className="button-strict">Buy on Uniswap</Button>
</div>
<div className="col-100%">  
<Button className="button-strict">Buy on Pancakeswap</Button>
</div>  
</Col>

</Row> 
</div>

<div className="tab-col">

<Row className="descr-btn">
<Col md={12}>
<div dangerouslySetInnerHTML={{__html: dealsDetails.Description}}>
</div>  
</Col>
</Row>

</div>
</div>

</Container>
</div> 
</div>
    </>)
}

export default Contribute