import Web3 from 'web3';
import {addressUSDTToken,abiUSDTToken} from './ETHContractData';

let web3Eth

if(typeof window!=='undefined' && typeof window.web3 !== 'undefined'){
    web3Eth = new Web3(window.web3.currentProvider);
} else{
    //on  the browser or user is not running metamask
    alert("Metamask wallet not found! Please make sure wallet is installed and running!");
}
// web3Eth.eth.net.getNetworkType().then(console.log);//ETH Network

export default new web3Eth.eth.Contract(abiUSDTToken,addressUSDTToken);
