import React, { useEffect, useState } from 'react';
import {Button, Spinner} from "react-bootstrap";
import { toast } from 'react-toastify'; //toster
import Web3 from 'web3';

import ETHBulkContract from '../contracts/ETHBulkContract';
import BSCBulkContract from '../contracts/BSCBulkContract';

import ETHimage from '../assects/ethereum.svg'
import BSCimage from '../assects/binance.svg'

let web3;

const  ConnectWallet=(props)=> {
  const [loading,setLoading] = useState(false);//Loading bar

  useEffect(()=>{
    if(window.ethereum) {
      window.ethereum.on('chainChanged', () => {
        window.location.reload();
      })
    }
  })

    async function connect(){
      setLoading(true)
      //connect to web3
      if(typeof window!=='undefined' && typeof window.web3 !== 'undefined'){
        //we are in the browser and metamask is running
          //ETH test net 3,  // mainnet 1
          // BSC test net 97  //mainnet 56
        web3 = new Web3(window.web3.currentProvider);
        let netId = await web3.eth.net.getId();
        var networkName="";
        console.log(netId);
        switch (netId) {
          case 1:
            networkName='Eth'
            console.log('connected to Ethereum mainnet')
            break
          case 56:
            networkName='BSC'
            console.log('connected to BSC mainnet')
            break
          case 3:
            networkName='Eth'
            console.log('connected to Ethereum Ropstern test network')
            break
          case 97:
            networkName='BSC'
            console.log('connected to BSC mainnet')
            break
          default:
            toast.error('Please select mainnet first')
            setLoading(false)
            return;
        }
        //set details
        //log network
        // web3.eth.net.getNetworkType().then(console.log);
        let accounts;
        try{
          accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
        } catch(error){
          if(error.code===4001){
            toast.error("Please allow metamask to connect")
            }
            setLoading(false)
            return;
          // console.log(error)
        }
        // console.log(accounts)
        if(accounts.length<1){
          setLoading(false)
          toast.error('Please unlock metamask wallet first!')
          return;
        }
        let weiBalance= await web3.eth.getBalance(accounts[0]);
        let balance=truncate(web3.utils.fromWei(weiBalance, 'ether'),4);
        // console.log(balance)
        // console.log('Checking tier');
        var token=0;
        try{
        var tokenEth = await ETHBulkContract.methods.balanceOf(accounts[0]).call();
        var tokenBSC = await BSCBulkContract.methods.balanceOf(accounts[0]).call();
        token = parseInt(tokenEth) + parseInt(tokenBSC);
        // console.log("You have\n TokenEth "+tokenEth+"\nTokenBSC "+ tokenBSC);
      }catch(error){
        console.log(error)
      }
        let tier = 0
        if(token>=1000000){
          tier=1
        } else if(token>=500000){
          tier=2
        } else if(token>=100000){
          tier=3
        } else if(token>=10000){
          tier=4
        }
        props.setWalletData({walletAddress:accounts[0],balance:balance,isConnected:true,networkName:networkName,ETHTokenCount:tokenEth,
        BSCTokenCount:tokenBSC,TotalTokenCount:token,tier:tier})
      } else{
        //on  the browser or user is not running metamask
        toast.error("Metamask wallet not found! Please make sure wallet is installed and running!");
      }
      setLoading(false)
    }
  
    async function CheckTier(){
      var tokenEth = await ETHBulkContract.methods.balanceOf(props.walletAddress).call();
      var tokenBSC = await BSCBulkContract.methods.balanceOf(props.walletAddress).call();
      props.setWalletData({ETHTokenCount:tokenEth,...props.walletData})
      props.setWalletData({BSCTokenCount:tokenBSC,...props.walletData})
      var token = parseInt(tokenEth) + parseInt(tokenBSC);
      props.setWalletData({TotalTokenCount:token,...props.walletData})
      // console.log("You have\n TokenEth "+tokenEth+"\nTokenBSC "+ tokenBSC);
      let tier = 0
      if(token>=1000000){
        tier=1
      } else if(token>=500000){
        tier=2
      } else if(token>=100000){
        tier=3
      } else if(token>=10000){
        tier=4
      }
      props.setWalletData({ETHTokenCount:tokenEth,BSCTokenCount:tokenBSC,TotalTokenCount:token,tier:tier,...props.walletData})
    }
    

    //Show balance with 4 decimal places
    function truncate(str, maxDecimalDigits) {
        if (str.includes('.')) {
            const parts = str.split('.');
            return parts[0] + '.' + parts[1].slice(0, maxDecimalDigits);
        }
        return str;
    }

    return (
      <>
      {
        loading?(
            <Spinner animation="border" role="status">
              <span className="visually-hidden">Loading...</span>
            </Spinner>
        ):(<>
          { props.walletData.isConnected?(
            <span class="bg-wallet">
              {props.walletData.networkName=="Eth"?(<img width="4%" src={ETHimage}/>):(<img width="6%" src={BSCimage}/>)}
              &ensp;{props.walletData.balance}&ensp;{props.walletData.networkName} &emsp;
              Address : {props.walletData.walletAddress.substr(0,5)}...{props.walletData.walletAddress.slice(-3)}
            </span>
          ):(
            <Button  className="button-strict" onClick={connect}>Connect Wallet</Button>        
          )}</>
        )
      }
      </>
    );
  }
  
  export default ConnectWallet
  