import React, { useState, useEffect,useContext } from "react";
import {Container, Row, Col, Button,Spinner,Dropdown,DropdownButton} from "react-bootstrap";
import { toast } from 'react-toastify'; //toster
import '../Style.css';
// import { LoginContext} from '../LoginContext';
// Route to contribute page 
import { Redirect, useHistory } from "react-router-dom";
//utils function 
import { isFinished, isActive, isUpcoming, getDealStauts } from "../utils";

function Dashboard({authorize}){
    const [dealsData,setDealsData] = useState([{}]);
    const [tabIndex,setTabIndex] = useState(0);
    const [loading,setLoading] = useState(false);//Loading bar
    // jump to contribute page
    const history = useHistory()
    // const [authorizeLogin,setAuthorizeLogin]=useContext(LoginContext)

    const getDeals = async ()=>{
        // API call to fetch deals
        //Test data
        // setDealsData(result.res)
        await fetch('https://ido-api-services.herokuapp.com/get-all-deals?filter={"offset": 0,"limit": 100,"skip": 0,"order": "string","where": {"additionalProp1": {}},"fields": {"Name": true,"id": true,"Ticker": true,"TotalRaize": true,"Currency": true,"Start": true,"End": true,"AllocationForTier1": true,"AllocationForTier4": true,"Description": true,"Icon": true,"CurrentRaize": true}}').then(res=>res.json()).then(
          (result)=>{
                    //set return data into dealsData object
                if(result.success){
                    if(tabIndex==0){ //set all deals
                        // if(result.res.length<1)
                        //     toast.info("No data found",{autoClose:2000})
                        setDealsData(result.res);
                        setLoading(false)
                    }else{
                        let data=[];
                        if(tabIndex==1){ //set active deals only
                            result.res.forEach((item) => {
                                if(isActive(item.Start,item.End)){
                                    data.push(item);
                                }
                            }); 
                        } else if(tabIndex==2){ //set upcoming deals only
                            result.res.forEach((item) => {
                                if(isUpcoming(item.Start)){
                                    data.push(item);
                                }
                            }); 
                        } else if(tabIndex==3){ //set finished deals only
                            result.res.forEach((item) => {
                                if(isFinished(item.End)){
                                    data.push(item);
                                }
                            }); 
                        }
                        // if(data.length<1)
                        //     toast.info("No data found",{autoClose:2000})
                        setDealsData(data);
                        setLoading(false)
                    }
                }else{
                    toast.error('Error while loading deals!');
                }
            }
        )
    }

    const deleteDeal= async(id)=>{
        // console.log(id)
        let token=localStorage.getItem('token')
        fetch('https://ido-api-services.herokuapp.com/delete-deals/'+id, {
                method: 'delete',
                headers: { 'Content-Type': 'application/json',
                    'Authorization': 'Bearer '+token  },
                }).then((response) => response.json())
                .then((responseJson) => {
                console.log(responseJson);
                if(responseJson.success)
                    toast.success(responseJson.message)
                else
                    toast.error(responseJson.error.message)
                getDeals()
                })
                .catch((error) => {
                console.error(error);
                });
    }

    const handleDealType= async (index)=>{
        // Handle tab selected
        if(index!==tabIndex){
            setLoading(true)
            setTabIndex(index);
        }
    }

    useEffect(()=>{
        setLoading(true)
        getDeals();
        // console.log(JSON.stringify(dealsData));
    },[tabIndex]);

return(<>
{authorize?(

<div className="dashboard">
    <Container className="maine-content">

    <Row>
        <div>
        <div style={{float:'left'}}>
            <h2>Deals</h2>
        </div>
        <div style={{float:'right'}}>
        <DropdownButton variant="secondary" title="Admin">
            <Dropdown.Item onClick={e=>history.push('/update-admin')}>Update Password</Dropdown.Item>
            <Dropdown.Item  onClick={e=>{localStorage.removeItem('token'); history.push('/login')}}>Logout</Dropdown.Item>
        </DropdownButton>
        </div>
        </div>
    </Row>


    <Row>
        <div>
        <div style={{float:'left',width:'90%'}}>
            {/* Options */}
            <div className="tab-row">
                <a href="#" className={tabIndex==0?"active":""} onClick={() => handleDealType(0)}>All Deals</a>
                <a href="#" className={tabIndex==1?"active":""} onClick={() => handleDealType(1)}>Active</a>
                <a href="#" className={tabIndex==2?"active":""} onClick={() => handleDealType(2)}>Upcoming</a>
                <a href="#" className={tabIndex==3?"active":""} onClick={() => handleDealType(3)}>Finished</a>
            </div>
            {/* tab row closed */}
        </div>
        <div style={{float:'right'}}>
            <Button className="button-strict" onClick={e=>history.push('/create')}>+ Add Deal</Button>
        </div>
        </div>
    </Row>

    {/* Deals Cards */}
    <div className="tab-content">
        <Row>
        {/* Loading bar */}
        {loading?
        (
            <div>
                <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
            </div>
        ):(<>
        {/* For Deals set the data */}
        {dealsData.map((data, key) => {
            return (
                    <Col md={4}>
                        <div className="tab-col">
                            <div className="user-name-row">
                                <img src={"data:image/png;base64,"+data.Icon}/>
                                <p>{data.Name}&emsp;<span>{data.Ticker}</span> </p>
                            </div>

                            <div className="row-box">
                                <p className="text-heading">Total Raize</p> 
                                <p className="amount">{data.TotalRaize} {data.Currency}</p>  
                            </div>

                            <div className="row-box">
                                <p className="text-heading">Max Allocaion</p> 
                                <p className="amount">{data.AllocationForTier1} {data.Currency}</p>  
                            </div>

                            <div className="row-box">
                                <p className="text-heading">Min allocation</p> 
                                <p className="amount">{data.AllocationForTier4} {data.Currency}</p>  
                            </div>

                            {isFinished(data.End)?(
                                <div className="finished-row finished">
                                    <span className="icon"><img  src={"data:image/png;base64,"+data.Icon}/></span>
                                    <Button className="button-strict"><span></span> Finished </Button>
                                </div>
                            ):(
                                <div className="finished-row">
                                    <span className="icon"><img  src={"data:image/png;base64,"+data.Icon}/></span>
                                    <Button className="button-strict"><span></span>
                                        {getDealStauts(data.Start,data.End)}
                                    </Button>
                                </div>
                            )}
                            
                            <div className="finished-amt">
                                <p>100%</p>  <p className="amt">{data.CurrentRaize}/{data.TotalRaize}</p> 
                            </div>

                            <div className="button-row">
                                <Button className="button-strict" onClick={() => deleteDeal(data.id)}>Delete Deal</Button>
                                <Button className="button-strict btn-right" onClick={e=>history.push('/edit/'+data.id)}>Edit Deal</Button>
                            </div>

                        </div>
                    </Col>
                );
        })}
        </>
        )}
        </Row>  
    </div>

    </Container>
</div> 
):(
    <Redirect to="/login"></Redirect>
)}

</>)
}

export default Dashboard
